import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import './Login.css';
import {auth} from "./firebase"

function Login() {
    const history=useHistory();

    const [email,setemail]=useState("");

    const [password,setpassword]=useState("")


    const login = (event) =>{
        event.preventDefault();
        // do the login logic
    
        auth.signInWithEmailAndPassword(email,password)
          .then((auth) => {
            //   logged in, redirect to homepage
            history.push("/");
          })
          .catch((e)=> alert(e.message))

    }
    const register =(event)=>{
        event.preventDefault();
        // do the register logic
        auth.createUserWithEmailAndPassword(email,password)
        .then((auth) => {
            // create a user and logged in,redirect to homepage
            history.push("/");
        })
        .catch((e)=> alert(e.message));
    }

    return (
        <div className="login">
            <Link to="/">
               <img className="login__logo" src="data:image/png;base64," alt="xyz"/>
            </Link>
            <div className="login__container">
                <h1>Sign in</h1>
                <form>
                    <h5>Email</h5>
                    <input value={email} onChange={event =>setemail(event.target.value)} type="email"/>
                    <h5>Password</h5>
                    <input value={password} onChange={event => setpassword(event.target.value)} type="password"/>
                    <button onClick={login} type="submit" className="login__signinbuttton" >Sign in</button>
                </form>
                <p>
                    By signing in you agree to Amazon's condition of code of conduct, 
                    click here to see our privacy ntice, our cookies notice and outline notice
                </p>
                <button onClick={register} className="login__registerbutton"> Create your Amazon account</button>
            </div>
        </div>
    )
}

export default Login
